.modal {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100vw;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.4);
  
    &_content {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 30px;
      border-radius: 10px;
      background-color: white;
    }
  
    &_icon {
      width: 60px;
      height: 60px;
      
      margin-bottom: 20px;
    }
  
    &_title {
      font-size: 20px;
  
      margin-bottom: 20px;
    }
  
    &_text {
      font-size: 14px;
      color: rgb(196, 192, 192);
      margin-bottom: 30px;
    }
  
    &_button {
      padding: 10px 0;
      width: 100%;
      background-color: #82ce34;
      color: white;
      border: none;
      border-radius: 5px;
  
      transition: 0.5s;
  
      &:hover {
        background-color: #6fb32b;  
      }

      margin-bottom: 5px;
    }
  }